@import url(https://fonts.googleapis.com/css?family=Ubuntu+Mono);
@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.terminal {
  width: 100%;
  height: 100%;
  position: absolute;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.5); }
  .terminal .toolbar {
    background: linear-gradient(#504b45 0%, #3c3b37 100%);
    width: 100%;
    padding: 0 8px;
    box-sizing: border-box;
    height: 25px;
    display: flex;
    align-items: center;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px; }
    .terminal .toolbar .user {
      color: #d5d0ce;
      font-size: 12px;
      line-height: 14px;
      margin-bottom: 10px;
      padding-right: 48px;
      margin: auto; }
    .terminal .toolbar .buttons {
      display: flex;
      align-items: center; }
      .terminal .toolbar .buttons button {
        width: 12px;
        height: 12px;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100%;
        padding: 0;
        font-size: 7px;
        color: #1d1717;
        background: linear-gradient(#7d7871 0%, #595953 100%);
        text-shadow: 0px 1px 0px rgba(255, 255, 255, 0.2);
        box-shadow: 0px 0px 1px 0px #41403A, 0px 1px 1px 0px #474642;
        border: none;
        margin-right: 4px; }
        .terminal .toolbar .buttons button:hover {
          cursor: pointer; }
        .terminal .toolbar .buttons button:focus {
          outline: none; }
        .terminal .toolbar .buttons button.exit {
          background: #f25d2b;
          background: linear-gradient(#f37458 0%, #de4c12 100%);
          background-clip: padding-box;
          font-size: 10px;
          padding: 0 2px 2px 0; }
        .terminal .toolbar .buttons button.minimize {
          padding-top: 1px; }
  .terminal .terminalBody {
    background: rgba(56, 4, 40, 0.9);
    height: inherit;
    overflow-wrap: normal;
    overflow-y: scroll;
    overflow-x: clip;
    margin-top: -1px;
    padding-top: 2px;
    padding-left: 2px;
    font-family: 'Ubuntu mono';
    color: #ddd;
    font-size: 15px;
    cursor: default !important; }
    .terminal .terminalBody .prompt {
      margin-top: 10px;
      display: flex;
      overflow-x: hidden;
      overflow-wrap: break-word;
      width: inherit; }
    .terminal .terminalBody .user {
      color: #87d441; }
    .terminal .terminalBody .location {
      color: #6d85a9; }
    .terminal .terminalBody .command {
      margin-left: 5px; }
    .terminal .terminalBody .cursor {
      height: 15.2px;
      width: 7px;
      background: white;
      display: block;
      -webkit-animation: 450ms ease infinite alternate blink;
              animation: 450ms ease infinite alternate blink; }

@-webkit-keyframes blink {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes blink {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
    .terminal .terminalBody input {
      background: none;
      border: none;
      height: 15px;
      width: 100%;
      color: #ddd; }

body {
  font-family: 'Ubuntu'; }

.App {
  overflow: hidden; }

.desktop {
  background: linear-gradient(45deg, #57003f 0%, #f57453 100%);
  position: relative;
  overflow: hidden;
  height: calc(100vh - 4rem);
  width: calc(100vw - 4rem);
  padding: 2rem; }

